.addCell {
  position: relative;
  opacity: 0;
  transition: opacity 0.3s ease-in 0.1s;
  margin: 10px 0;
}

.addCell:hover {
  opacity: 1;
}

.addCell.forceVisible {
  opacity: 1;
}

.addCell .divider {
  position: absolute;
  top: 50%;
  right: 2.5%;
  left: 2.5%;
  bottom: 50%;
  border-bottom: 1px solid gray;
  width: 90%;
  z-index: -1;
}

.addCell .addButtons {
  display: flex;
  justify-content: center;
}

.addCell .addButtons button {
  margin: 0 20px;
}
